}

.nav__name::before {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  transform: scaleX(0);
  transition: transform 0.4s ease-in-out;
  transform-origin: left;
}

.nav__name:hover::before {
  transform: scaleX(1);
}

